html {
  background-color:#F9F6F1;
}

.containers {
  /* padding-left: 300px;
  padding-right: 300px; */
  /* padding-top: 40px */
  padding-left: 18.75rem;
  padding-right: 18.75rem;
  padding-top: 2.5rem
}

/* Contains title */
.firstContainer {
  color: black;
}

.secondContainer {
  color: #1b1b1bbf;
  text-align: left;
  padding-right: 100px;
  margin-top: 40px;
}

a {
  transition: all .2s ease-in-out;
}

a:link {
  color: #1b1b1bbf;
}

a:visited {
  color: #1b1b1bbf;
}

a:hover {
  color: cadetblue;
}

.secondContainer p{
  /* font-size: 25px; */
  font-size: 1.5625rem;
  margin: 0px;
  /* padding-bottom: 25px; */
  padding-bottom: 1.5625rem;
}

.media-icons {
  padding-top: 3.125rem;
}

.media-icons img{
  /* height: 75px; */
  /* width: 75px; */
  /* padding-right: 35px; */
  height: 4.6875rem;
  width: 4.6875rem;
  padding-right: 2.1875rem;
  transition: all .1s ease-in-out;
}

.media-icons img:hover{
  /* height: 80px;
  width: 80px; */
  height: 5rem;
  width: 5rem;
  rotate: 10deg;
}

.media-icons a:focus {
  outline: none !important;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
